import React, { Component } from 'react';
import "./Quiz.css";
import birdsData from '../../data/birds';
import AudioPlayer from '../Audio/AudioPlayer';

export default class Quiz extends Component {

  render() {

    const { page, id } = this.props;

    return (
      <div className="random-bird jumbotron rounded">
        <img className="bird-image"
             src={birdsData[page][id].image} alt={birdsData[page][id].name} />
        <div>          
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              <h3>{birdsData[page][id].name}</h3>
            </li>
            <li className="list-group-item">
              <AudioPlayer url={birdsData[page][id].audio} />
            </li>
          </ul>
        </div>
      </div>

    );
  }
}